#reset-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    font-family: 'Spoqa Han Sans Neo';
}

.reset-card {
    width: 900px;
    height: 870px;

    border-radius: 15px;
    border: 3px solid rgba(24, 160, 251, 1);

    display: flex;
    align-items: center;
    flex-direction: column;

    background-color: rgba(232, 241, 255, 1);
    border-radius: 30px;

    position: relative;
}

.reset-button-box {
    margin: 15px 25px 15px 823px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-close-button {
    width: 52px;
    height: 40px;

    background-color: rgba(255, 255, 255, 1);
    border: 3px solid rgba(24, 160, 251, 1);
    border-radius: 10px 10px;

    font-family: 'Spoqa Han Sans Neo';
    font-size: 20px;
}

.modal-close-button:hover {
    cursor: pointer;
    background-color: rgba(24, 160, 251, 0.50);
}

.reset-control-box {
    display: flex;
    flex-direction: column;

    gap: 35px;
}

.study-title-container {
    height: 35px;

    display: flex;
    align-items: center;
    flex-direction: row;

    gap: 13px;
}

.study-title {
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.31px;

    font-size: 20px;
}

.study-title-write-container {
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: space-between;

    position: relative;

    border-radius: 5px;

    font-family: 'Spoqa Han Sans Neo';
}

.study-title-write {
    width: 490px;
    height: 100%;

    display: flex;

    padding-left: 20px;

    border-radius: 5px;
    border: 1px solid rgba(24, 160, 251, 1);

    font-family: 'Spoqa Han Sans Neo';
    font-size: 15px;
    font-weight: 400;
}

.study-title-write::placeholder {
    color: rgba(207, 207, 207, 1);
}

.study-title-write-count {
    position: absolute;
    padding-right: 15px;
    top: 15%;
    right: 0;
    font-size: 20px;
    font-weight: 400;
}

.study-title-write-error-box {
    margin-top: -30px;
    margin-bottom: -10px;
    margin-left: 123px;
    width: auto;
    height: 14px;

    color: rgba(255, 0, 0, 0.70);
}

.study-title-write-error-message {
    width: auto;
    height: 14px;

    color: rgba(255, 0, 0, 0.70);
    font-family: Spoqa Han Sans Neo;
    font-size: 12.24px;
    font-weight: 700;
    line-height: 140%;

    text-wrap: nowrap;
}

.study-period-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 46px;
    gap: 45px;
}

.study-period-title {
    display: flex;
    align-items: center;

    color: rgba(0, 0, 0, 1);
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.31px;

    font-size: 20px;
}

.study-period-end-date-title-box {
    display: flex;
    align-items: center;
    flex-direction: row;

    gap: 10px;

    text-align: center;
    font-weight: 400;
    letter-spacing: -0.222px;
}

.study-count-setting {
    width: auto;
    height: 30px;

    display: flex;
    align-items: center;
}

.study-count-title {
    display: flex;
    align-items: center;

    color: rgba(0, 0, 0, 1);

    font-family: Spoqa Han Sans Neo;
    font-size: 20px;
    font-weight: 400;

    letter-spacing: -0.31px;
    margin-right: 15px;
}

.study-count-add-icon {
    width: 30px;
    height: 30px;

    background-image: url(assets/plus-square-icon.png);
    background-position: 50% 50%;
    background-size: 100% 100%;

    margin-right: 5px;
}

.study-count-add-icon:hover {
    cursor: pointer;
}

.study-count-contents {
    width: 50px;
    height: 30px;

    color: rgba(0, 0, 0, 1);

    font-size: 15px;
    font-weight: 400;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: rgba(255, 255, 255, 1);
    border: 1px solid rgba(24, 160, 251, 1);
    border-radius: 5px;
}

.study-count-minus-icon {
    width: 30px;
    height: 30px;
    margin-left: 5px;

    background-image: url(assets/minus-square-icon.png);
    background-position: 50% 50%;
    background-size: 100% 100%;
}

.study-count-minus-icon:hover {
    cursor: pointer;
}

.study-count-error-message {
    margin-left: 7px;
    color: rgba(255, 0, 0, 0.70);
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
}

.study-modify-category-container {
    display: flex;
    align-items: center;
    flex-direction: row;
}

.study-modify-category-title {
    font-size: 20px;
    font-weight: 400;
    display: flex;
    align-items: center;
    justify-self: center;
    margin-right: 17px;

    text-wrap: nowrap;
}

.study-category-contents {
    width: 85px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;

    margin-left: 17px;
    text-wrap: nowrap;
}

.study-category-icon {
    width: 30px;
    height: 30px;

    background-image: url(assets/down-icon.png);
    background-position: 50% 50%;
    background-size: 100% 100%;

    margin-left: 19px;
}

.study-category-icon:hover {
    cursor: pointer;
}

.study-open-container {
    display: flex;
    flex-direction: row;
}

.study-open-title {
    color: rgba(0, 0, 0, 1);

    font-size: 20px;
    font-weight: 400;
    letter-spacing: -0.3px;

    margin-right: 31px;
}

.study-open {
    width: 150px;
    height: 30px;

    border: none;
    margin-right: 18px;

    border-radius: 5px;
    border: 0.5px solid rgba(24, 160, 251, 0.7);
    background-color: rgba(255, 255, 255, 1);

    color: rgba(0, 0, 0, 1);
    font-family: Spoqa Han Sans Neo;
    font-size: 15px;
    font-weight: 400;
    letter-spacing: -0.225px;
}

.study-open-select {
    width: 150px;
    height: 30px;

    border: none;
    margin-right: 18px;

    color: rgba(0, 0, 0, 1);
    font-family: Spoqa Han Sans Neo;
    font-size: 15px;
    font-weight: 400;
    letter-spacing: -0.225px;

    border-radius: 5px;
    border: 0.5px solid rgba(24, 160, 251, 0.7);
    background-color: rgba(24, 160, 251, 0.7);
}

.study-open-select:hover {
    cursor: pointer;
    background-color: rgba(207, 207, 207, 1);
}

.study-open:hover {
    cursor: pointer;
    background-color: rgba(207, 207, 207, 1);
}

.study-close-box {
    border-radius: 5px;

    display: flex;
    flex-direction: column;
}

.study-close {
    width: 150px;
    height: 30px;
    border-radius: 5px;
    border: 0.5px solid rgba(24, 160, 251, 1);
    background-color: rgba(255, 255, 255, 1);

    color: rgba(0, 0, 0, 1);
    font-family: 'Spoqa Han Sans Neo';
    font-size: 15px;
    font-weight: 400;
    letter-spacing: -0.225px;
}

.study-close:hover {
    cursor: pointer;
    background-color: rgba(207, 207, 207, 1);
}

.study-close-select {
    width: 150px;
    height: 30px;
    border-radius: 5px;
    border: 0.5px solid rgba(24, 160, 251, 1);
    background-color: rgba(24, 160, 251, 0.7);

    color: rgba(0, 0, 0, 1);
    font-family: 'Spoqa Han Sans Neo';
    font-size: 15px;
    font-weight: 400;
    letter-spacing: -0.225px;
}

.study-close-select:hover {
    cursor: pointer;
    background-color: rgba(207, 207, 207, 1);
}

.study-close-password {
    display: flex;
    flex-direction: row;
}

.study-close-password-write {
    margin-top: 8px;
    margin-bottom: -50px;

    width: 150px;
    height: 30px;
    border-radius: 5px;
    border: 0.5px solid rgba(24, 160, 251, 1);
    background-color: rgba(255, 255, 255, 1);
    
    font-family: 'Spoqa Han Sans Neo';
    font-size: 15px;
}

.password-icon {
    margin-top: 10px;

    width: 30px;
    height: 30px;

    position: absolute;
    transform: translateX(410%);

    background-image: url(assets/copy-icon.png);
    background-position: 50% 50%;
    background-size: 100% 100%;
}

.password-icon:hover {
    cursor: pointer;
}

.study-cover-modify-image-box {
    width: 347px;
    height: 170px;

    display: flex;
    flex-direction: row;
    gap: 7px;
    background: none;
}

.study-cover-image-title {
    color: rgba(0, 0, 0, 1);

    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.3px;

    text-wrap: nowrap;
}

.study-cover-image-contents {
    width: 170px;
    height: 170px;
    
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    border: 0.5px solid rgba(24, 160, 251, 1);
    position: relative;
}

.study-cover-profile-default-image {
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    background-position: 50% 50%;
    background-size: 100% 100%;
}

.study-cover-profile-image {
    width: 100%;
    height: 100%;

    border-radius: 10px 10px;

    background-position: 50% 50%;
    background-size: 100% 100%;
}

.change-controll {
    position: absolute;

    width: 48px;
    height: 16px;
    right: 3px;
    bottom: 4px;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 8.864px;
    border: 0.5px solid rgba(24, 160, 251, 1);
    background-color: rgba(255, 255, 255, 1);

    color: rgba(0, 0, 0, 1);
    text-align: center;
    font-family: 'Spoqa Han Sans Neo';
    font-size: 8.7px;
}

.change-controll:hover {
    opacity: 0.85;
    cursor: pointer;
}

.study-reset-buttton-box {
    margin-top: 160px;

    width: 700px;
    height: 60px;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 5px;
    border: 2px solid rgba(24, 160, 251, 1);

    background-color: rgba(255, 255, 255, 1);

    font-size: 21px;
    font-weight: 400;
}

.study-reset-buttton-box:hover {
    cursor: pointer;
    background-color: rgba(24, 160, 251, 0.7);
}